<template>
  <div class="finance-custom-service full-screen">
    <div class="page-panel">
      <pem-scroll-view 
        class="scroll-view"
        ref="scroll">
        <div class="wrap">
          <div class="wrapper">
            <img class="title-img" src="./title.png" alt="">
            <div class="content">
              <div class="title">
                <img src="./title_l@2x.png" alt="">
                <span>产品特色</span>
                <img src="./title_r@2x.png" alt="">
              </div>
              <ul class="clearfix feature">
                <li>
                  <img src="./icon_prioject@2x.png" alt="">
                  <div>优质项目<br>抢先阅</div>
                </li>
                <li>
                  <img src="./icon_shangchuan@2x.png" alt="">
                  <div>融资项目<br>一键上传</div>
                </li>
                <li>
                  <img src="./icon_huodong@2x.png" alt="">
                  <div>精选活动<br>优先参与</div>
                </li>
                <li>
                  <img src="./icon_zixun@2x.png" alt="">
                  <div>海量资讯<br>实时查看</div>
                </li>
              </ul>
              <div class="title">
                <img src="./title_l@2x.png" alt="">
                <span>平台数据</span>
                <img src="./title_r@2x.png" alt="">
              </div>
              <table class="datas">
                <tr>
                  <th>
                    <span class="num">87500</span>
                    <span class="add">+</span>
                  </th>
                  <th>
                    <span class="num">42600</span>
                    <span class="add">+</span>
                  </th>
                  <th>
                    <span class="num">27300</span>
                    <span class="add">+</span>
                  </th>
                </tr>
                <tr>
                  <td>融资项目</td>
                  <td>活跃投资人</td>
                  <td>投资机构</td>
                </tr>
              </table>
              <div class="title">
                <img src="./title_l@2x.png" alt="">
                <span>成功案例</span>
                <img src="./title_r@2x.png" alt="">
              </div>
              <div class="cases">
                <img src="./anli1@2x.png" alt="">
                <img src="./anli2@2x.png" alt="">
                <img src="./anli3@2x.png" alt="">
                <img src="./anli4@2x.png" alt="">
                <img src="./anli5@2x.png" alt="">
                <img src="./anli6@2x.png" alt="">
              </div>
            </div>
          </div>
        </div>
    </pem-scroll-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { setTitle } from 'common/js/util'

export default {
  data () {
    return {
      list: []
    }
  },
  components: {
    PemScrollView
  },
  created () {
    this._setTitle('找项目、找投资人信息，就用项目工场')
    setTimeout(() => {
      this.$refs.scroll.refresh()
    }, 50)
  },
  methods: {
    _setTitle (title) {
      setTitle(title)
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import "~common/stylus/mixin"
  .page-panel
    top: 0
  .wrapper
    position: relative
    width: 100%
    height: 18.54rem
    bg-image('./bg')
    background-size: 100% 100%
    background-repeat: no-repeat
    .title-img
      display: block
      width: 5.98rem
      height: 2.6rem
      margin: 0 auto
    .content
      position: absolute
      width: 6.74rem
      top: 5.6rem
      left: 0.38rem
      .title
        text-align: center
        padding-top: 0.3rem
        font-size: 0
        span,img 
          vertical-align: middle
        img 
          width: 0.72rem
          height: 0.16rem
        span
          display: inline-block
          font-family: PingFangSC-Medium;
          font-size: 0.36rem
          font-weight: bold
          color: #4E2705;
          letter-spacing: 0.05px
          height: 0.5rem
          line-height: 0.5rem
          padding: 0 0.1rem
      .feature
        padding: 0.4rem 0 0.3rem
        text-align: center
        li
          float: left
          width: 25%
          img
            width: 0.98rem
            height: 0.98rem
          div
            padding-top: 0.16rem
            font-family: PingFangSC-Medium
            font-size: 0.2rem
            color: #622E00
            letter-spacing: 0
            text-align: center
            line-height: 0.3rem
      .datas
        width: 5.9rem
        margin: 0.5rem auto 0.3rem
        border: 0.5px solid #CDCDCD
        border-radius: 0.08rem
        text-align: center
        tr
          &:nth-child(1)
            border-bottom: 0.5px solid #CDCDCD
          td,th
            &:nth-child(2)
              border-left: 0.5px solid #CDCDCD
              border-right: 0.5px solid #CDCDCD
        th,td
          height: 0.82rem
          vertical-align: middle
        th
          font-size: 0
          span
           vertical-align: middle
          .num
            font-family: FZLTTHK--GBK1-0
            font-size: 0.32rem
            font-weight: bold
            color: #FC8711
            letter-spacing: 0.04px
          .add
            font-family: PingFangSC-Medium
            font-size: 0.28rem
            color: #FC8711;
            letter-spacing: 0.04px
        td
          font-family: PingFangSC-Medium
          font-size: 0.28rem
          color: #622E00
          letter-spacing: 0.19px
      .cases
        position: relative
        width: 5.4rem
        height: 4.42rem
        margin: 0.3rem auto 0
        img
          position: absolute
          width: 2.66rem
          height: 1.46rem
          &:nth-child(odd)
            left: 0
          &:nth-child(even)
            right: 0
          &:nth-child(1), &:nth-child(2)
            top: 0
          &:nth-child(3), &:nth-child(4)
            top: 1.48rem
          &:nth-child(5), &:nth-child(6)
            bottom: 0
</style>



